<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-btn color="accent" large @click.stop="showHomeworkForm = true">
          Yeni Ödev
        </v-btn>
      </v-col>
      <v-col cols="2">
        <v-btn color="accent" large @click.stop="openStudentsDialog()">
          Öğrenciler
        </v-btn>
      </v-col>

      <v-col cols="2">
        <v-btn color="accent" large @click.stop="showUploadDialog = true">
          Dosya Yükle
        </v-btn>
      </v-col>

      <HomeworkForm v-model="showHomeworkForm" :offerId="offerId" />

      <h1>{{ course.name }}</h1>
    </v-row>

    <v-divider></v-divider>

    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(homework, index) in homeworks"
        :key="index"
        @click="goToHomework(homework)"
      >
        <v-card color="primary" shaped>
          <v-card-title>
            <v-icon class="mr-2">mdi-book</v-icon>
            {{ homework.name }}
            <v-btn icon @click="deleteHomework($event, homework.id)">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>

    <v-divider></v-divider>

    <v-row>
      <v-col cols="2">
        <v-btn color="success" @click="openBulkMarkDialog">
          <v-icon left>mdi-pen-plus</v-icon>
          Add Mark
        </v-btn>
      </v-col>
      <v-col cols="2">
        <v-btn color="success" @click="save">
          <v-icon left>mdi-content-save</v-icon>
          Save
        </v-btn>
      </v-col>
      <v-divider></v-divider>
    </v-row>

    <v-divider></v-divider>

    <v-row>
      <v-col cols="6" v-for="(student, index) in students" :key="index">
        <v-card>
          <v-card-title>
            <v-row>
              <v-col cols="4">
                {{ student.firstName + " " }}
                <span v-if="student.middleName">{{
                  student.middleName + " "
                }}</span>
                {{ student.lastName }}
                <v-btn color="success" icon @click="addNewMark(index)">
                  <v-icon>mdi-pen-plus</v-icon>
                </v-btn>
              </v-col>
              <v-col
                cols="4"
                v-for="(mark, index) in student.marks"
                :key="index"
              >
                <v-card light>
                  <v-card-text>
                    <v-text-field
                      prepend-icon="mdi-rename-box"
                      dense
                      outlined
                      placeholder="Name"
                      v-model="mark.name"
                    >
                    </v-text-field>
                    <v-text-field
                      prepend-icon="mdi-numeric"
                      dense
                      outlined
                      type="number"
                      max="100"
                      min="0"
                      placeholder="Points"
                      v-model="mark.points"
                    >
                    </v-text-field>
                    <v-text-field
                      prepend-icon="mdi-brightness-percent"
                      dense
                      placeholder="Percentage"
                      type="number"
                      max="100"
                      min="0"
                      v-model="mark.percentage"
                      outlined
                    >
                    </v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="showUploadDialog" max-width="40vw">
      <v-card>
        <v-card-title>
          <v-file-input
            v-model="file"
            show-size
            label="File input"
          ></v-file-input>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green" @click="uploadFile">
            Create
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showStudentsDialog" scrollable max-width="60vw">
      <v-card>
        <v-card-title>
          <v-btn
            icon
            color="error"
            @click="unenrollStudent()"
            :disabled="selectedStudents.length < 1"
          >
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="studentSearch"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          >
          </v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="students"
          :search="studentSearch"
          show-select
          v-model="selectedStudents"
        >
          <template v-slot:item.action="{ item }">
            <v-icon @click="goToStudentProfile(item)">
              mdi-account-search
            </v-icon>
          </template>
        </v-data-table>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showBulkMarkDialog" scrollable max-width="20vw">
      <v-card>
        <v-card-title>
          <v-text-field
            prepend-icon="mdi-rename-box"
            dense
            outlined
            placeholder="Name"
            v-model="bulkMarkName"
          >
          </v-text-field>
          <v-row>
            <v-col cols="6">
              <v-text-field
                prepend-icon="mdi-numeric"
                dense
                outlined
                type="number"
                max="100"
                min="0"
                placeholder="Points"
                v-model="bulkMarkPoints"
              >
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                prepend-icon="mdi-brightness-percent"
                dense
                placeholder="Percentage"
                type="number"
                max="100"
                min="0"
                v-model="bulkMarkPercentage"
                outlined
              >
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green" @click="createBulkMark">
            Create
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="snackbar"
      bottom
      :color="snackbarColor"
      :timeout="snackbarTimeout"
    >
      {{ snackbarText }}
      <v-btn text @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import OfferService from "../services/OfferService";
import CourseService from "../services/CourseService";

import HomeworkForm from "../components/HomeworkForm";
import HomeworkService from "../services/HomeworkService";
import StudentService from "../services/StudentService";

export default {
  components: {
    HomeworkForm
  },
  data() {
    return {
      showUploadDialog: false,
      showStudentsDialog: false,
      offer: {},
      file: {},
      course: {},
      homeworks: [],
      students: [],
      enrolleds: [],
      selectedStudents: [],
      studentSearch: "",
      offerId: Number,
      showHomeworkForm: false,
      showBulkMarkDialog: false,
      bulkMarkName: "Exam",
      bulkMarkPoints: 0,
      bulkMarkPercentage: "100",
      headers: [
        {
          text: "First Name",
          value: "firstName"
        },
        {
          text: "Middle Name",
          value: "middleName"
        },
        {
          text: "Last Name",
          value: "lastName"
        },
        { text: "Average", value: "average" },
        { text: "Actions", value: "action", sortable: false }
      ],
      snackbar: false,
      snackbarText: "Snackbar",
      snackbarColor: "primary",
      snackbarTimeout: 3000
    };
  },
  created() {
    this.offerId = this.$route.params.offerId;
    OfferService.GetStudents(this.offerId).then(students => {
      this.students = students;
      for (let i = 0; i < students.length; i++) {
        const current = students[i];
        StudentService.GetEnrolled(current.id, this.offerId).then(enrolled => {
          this.enrolleds.push(enrolled);

          if (!enrolled.marks) {
            enrolled.marks = [];
          }
          this.$set(this.students[i], "marks", enrolled.marks);
        });
      }
    });

    OfferService.GetOffer(this.offerId).then(offer => {
      this.offer = offer;
      OfferService.GetHomeworks(this.offerId).then(homeworks => {
        this.homeworks = homeworks;
      });
      CourseService.GetCourseById(offer.courseId).then(course => {
        this.course = course;
      });
    });
  },
  methods: {
    goToHomework(homework) {
      this.$router.push({
        name: "Assignments",
        params: { homeworkId: homework.id, homework: homework }
      });
    },
    deleteHomework(event, homeworkId) {
      event.preventDefault();
      HomeworkService.DeleteHomework(homeworkId);
    },
    unenrollStudent(offerId) {
      let studentIds = [];
      for (let i = 0; i < this.selectedStudents.length; i++) {
        const el = this.selectedStudents[i];
        studentIds.push(el.id);
      }

      OfferService.UnenrollStudents(offerId, studentIds).then(() => {
        // this.$router.go()
      });
    },
    goToStudentProfile(clickedRow) {
      this.$router.push({
        name: "ViewStudent",
        params: { studentId: clickedRow.id }
      });
    },
    openStudentsDialog() {
      this.showStudentsDialog = true;
    },
    openBulkMarkDialog() {
      this.showBulkMarkDialog = true;
    },
    createBulkMark() {
      for (let index = 0; index < this.students.length; index++) {
        this.addNewMark(index);
      }
      this.showBulkMarkDialog = false;
    },
    addNewMark(index) {
      this.students[index].marks.push({
        name: this.bulkMarkName,
        points: this.bulkMarkPoints,
        percentage: this.bulkMarkPercentage
      });
    },
    save() {
      for (let index = 0; index < this.students.length; index++) {
        const student = this.students[index];
        this.enrolleds.marks = student.marks;
        StudentService.UpdateEnrolled(this.enrolleds[index]).then(() => {
          this.snackbar = true;
          this.snackbarColor = "success";
          this.snackbarText = "Updated";
        });
      }
    },
    uploadFile() {
      OfferService.UploadFile(this.offerId, this.file).then(success => {
        this.snackbar = true;
        if (success) {
          this.snackbarColor = "success";
          this.snackbarText = "Uploaded.";
        } else {
          this.snackbarColor = "error";
          this.snackbarText = "Error uploading!";
        }
      });
    }
  }
};
</script>

