<template>
  <v-dialog v-model="show" max-width="50vw">
    <v-form ref="form" v-model="isValid" lazy-validation>
      <v-card class="pa-5" color="white">
        <div v-if="!completed">
          <!-- PAGINATION -->
          <div class="text-center">
            <v-pagination
              @input="changePage"
              v-model="page"
              :length="questions.length + 1"
            ></v-pagination>
          </div>
          <!-- TASK FIRSTS PAGE -->
          <div v-if="page == 1">
            <v-card-text>
              <v-text-field
                solo
                background-color="blue lighten-5"
                prepend-icon="mdi-rename-box"
                v-model="name"
                :counter="40"
                :rules="textRules"
                label="Ödev Adı"
                required
                class="mt-5"
              >
              </v-text-field>
              <v-row>
                <v-col cols="10">
                  <v-select
                    prepend-icon="mdi-rename-box"
                    outlined
                    background-color="blue lighten-5"
                    :items="Object.values(answerTypes)"
                    v-model="selectedType"
                  >
                  </v-select>
                </v-col>
                <v-col cols="2">
                  <v-btn fab small color="primary" @click.stop="addQuestion">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-checkbox
                v-model="lockAfterAnswering"
                label="Lock Assignment after Sending?"
              ></v-checkbox>
            </v-card-text>
          </div>
          <div v-else-if="!completed">
            <v-card-text>
              <v-row>
                <v-col cols="9">
                  <v-textarea
                    prepend-icon="mdi-rename-box"
                    :rules="textRules"
                    label="Question Text"
                    v-model="selectedQuestion.text"
                    background-color="blue lighten-5"
                    solo
                    rows="1"
                    auto-grow
                  >
                  </v-textarea>
                </v-col>
                <v-col cols="3">
                  <v-textarea
                    rows="1"
                    no-resize
                    label="Point"
                    v-model="selectedQuestion.point"
                    number
                    solo
                    background-color="blue lighten-5"
                  ></v-textarea>
                </v-col>
              </v-row>

              <div v-if="selectedQuestion.answerType == answerTypes.multiple">
                <v-btn
                  class="mb-5"
                  color="primary"
                  @click="addChoice(selectedQuestion.choices)"
                  >Add Choice</v-btn
                >
                <div
                  v-for="(choice, index) in selectedQuestion.choices"
                  :key="index"
                >
                  <v-textarea
                    :label="'Choice ' + (index + 1)"
                    solo
                    rows="1"
                    auto-grow
                    :rules="textRules"
                    background-color="blue lighten-5"
                    v-model="selectedQuestion.choices[index]"
                    append-icon="mdi-delete"
                    @click:append="
                      deleteChoice(index, selectedQuestion.choices)
                    "
                  ></v-textarea>
                </div>

                <v-row>
                  <v-radio-group v-model="selectedQuestion.answer">
                    <v-radio
                      v-for="(choice, index) in selectedQuestion.choices"
                      :key="index"
                      :label="choice"
                    >
                    </v-radio>
                  </v-radio-group>
                </v-row>
              </div>
              <div v-if="selectedQuestion.answerType == answerTypes.classic">
                <strong>
                  <h3>
                    Soruyu açık uçlu bırakmak istiyorsanız cevap girmeyin!
                  </h3>
                </strong>
                <v-textarea
                  solo
                  background-color="blue lighten-5"
                  label="Answer Text"
                  v-model="selectedQuestion.answer"
                  append-icon="mdi-file-compare"
                ></v-textarea>
              </div>
            </v-card-text>
          </div>
          <v-card-actions>
            <v-btn
              :disabled="!isValid"
              color="success"
              class="mr-4"
              @click="validate"
            >
              Complete
            </v-btn>

            <v-btn color="warning" class="mr-4" @click="reset">
              Reset Form
            </v-btn>
          </v-card-actions>
        </div>
      </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
import HomeworkService from "../services/HomeworkService";

export default {
  props: {
    value: Boolean,
    offerId: String
  },

  created() {
    this.selectedType = this.answerTypes.multiple;
  },

  data: () => ({
    completed: false,
    name: "",
    router: {},
    page: 1,
    textRules: [
      v => !!v || "Text is required",
      v => (v && v.length <= 40) || "Text must be less than 40 characters"
    ],
    answerTypes: {
      multiple: "multiple",
      classic: "classic"
    },
    selectedType: "",
    questions: [],
    selectedQuestion: {},
    isValid: false,
    lockAfterAnswering: false
  }),

  methods: {
    async validate() {
      if (this.$refs.form.validate()) {
        const homework = {
          name: this.name,
          description: this.description,
          questions: this.questions,
          offerId: this.offerId,
          lockAfterAnswering: this.lockAfterAnswering
        };

        HomeworkService.GiveHomework(homework).then(() => {
          this.$router.go();
        });
      }
    },

    reset() {
      this.questions = [];
      this.name = "";
      this.selectedType = this.answerTypes.multiple;
      this.answers = [];
      this.page = 1;
    },

    addQuestion() {
      const question = {
        text: "",
        answerType: this.selectedType,
        choices: [],
        point: 0
      };

      this.questions.push(question);
      this.page = this.questions.length + 1;
      this.selectedQuestion = this.questions[this.questions.length - 1];
    },

    removeQuestion(index) {
      this.questions.splice(index, 1);
    },

    addChoice(choices) {
      choices.push("");
    },

    deleteChoice(index, choices) {
      choices.splice(index, 1);
    },

    isNumber(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault();
      } else {
        return true;
      }
    },

    isBetween(evt) {
      evt = evt ? evt : window.event;
      if (evt == 100) {
        evt.preventDefault();
      }
    },

    changePage() {
      this.selectedQuestion = this.questions[this.page - 2];
    }
  },

  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      }
    }
  }
};
</script>